<script>
    import qs from "qs";

    export default {
        data() {
            return {
                num: 1,
                // 
                queryInfo: {
                    ip: "",
                    port: "",
                    pagenum: 1,
                    // 
                    pagesize: 10
                },
                iplist: [],
                total: 0,
                addDiglogVisable: false,
                addForm: {
                    taskname: "",
                    description: "",
                    cronspec: "now",
                    day: 1,
                    hour: 1,
                    cronspecmd: "",
                    thread: "100",
                    brute: "",
                    source: "1",
                    sourcecontent: ""
                },
                editForm: {},
                editDiglogVisable: false,

                // 
                addFormRules: {
                    taskname: [
                        {required: true, message: "", trigger: "blur"},
                        {
                            min: 1,
                            max: 20,
                            message: "220",
                            trigger: "blur"
                        }
                    ],
                    brute: [
                        {required: true, message: "", trigger: "blur"},
                        {
                            min: 2,
                            max: 18,
                            message: "218",
                            trigger: "blur"
                        }
                    ],
                    codetype: [
                        {required: true, message: "", trigger: "blur"},
                        {
                            min: 2,
                            max: 18,
                            message: "218",
                            trigger: "blur"
                        }
                    ],
                    filename: [
                        {required: true, message: "", trigger: "blur"}
                    ]
                }
            };
        },
        created() {
            this.getIpList();
        },
        methods: {
            formatterTaskType(val) {
                if (val == "ssh") {
                    return "ssh";
                } else if (val == "mysql") {
                    return "mysql";
                } else if (val == "ftp") {
                    return "ftp";
                } else if (val == "smb") {
                    return "smb";
                } else if (val == "mssql") {
                    return "mssql";
                } else if (val == "postgresql") {
                    return "postgresql";
                } else if (val == "redis") {
                    return "redis";
                } else if (val == "mongodb") {
                    return "mongodb";
                } else {
                    return "";
                }
            },
          formatterTaskStatus(val) {
            if (val == "now") {
              return "";
            } else {
              return "";
            }
          },
          formatterTaskCycle(val) {
            if (val == "now") {
              return "";
            } else {
              return val;
            }
          },
            async getIpList() {
                try {
                    const {data: res} = await this.$http.get("/crons", {
                        params: this.queryInfo
                    });
                    if (res.code !== 200) {
                        return this.$message.error("");
                    }
                    this.iplist = res.data.lists;
                    this.total = res.data.total;
                } catch (error) {
                    return this.$message.error("cookie");
                    error.message; // "Oops!"
                }
            },
            async getIpListSingle() {
                const {data: res} = await this.$http.get("/jobiplist", {
                    params: this.queryInfo
                });
                if (res.code !== 200) {
                    return this.$message.error("");
                }
                this.iplist = res.data.lists;
                this.total = res.data.total;
                //   console.log(res)
            },
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pagesize = newSize;
                console.log(newSize);
                this.getIpList();
            },
            // 
            handleCurrentChange(newPage) {
                this.queryInfo.pagenum = newPage;
                console.log(newPage);
                this.getIpList();
            },

            //
            diglogClose(operation) {
                if (operation === "add") {
                    this.$refs.addFormRef.resetFields();
                } else if (operation === "edit") {
                    this.$refs.editFormRef.resetFields();
                }
            },

            // 
            addList() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    const {data: resp} = await this.$http.post(
                        "/addcron",
                        qs.stringify(this.addForm),
                        {headers: {"Content-Type": "application/x-www-form-urlencoded"}}
                    );

                    if (resp.code == 401) {
                        this.$message.success("cookie");
                        console.log("resp");
                        console.log(resp);
                        return this.$message.error(resp.msg);
                    }
                    this.$refs.addFormRef.resetFields();
                    this.addDiglogVisable = false;
                    this.$message.success("");
                    this.getIpList();
                });
            },

            editDiglogInit(userInfo) {
                this.editForm = JSON.parse(JSON.stringify(userInfo));
                this.editDiglogVisable = true;
            },

            editListInfo() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    const {
                        data: resp
                    } = await this.$http.put(
                        `/jobiplist/${this.editForm.id}`,
                        qs.stringify(this.editForm),
                        {headers: {"Content-Type": "application/x-www-form-urlencoded"}}
                    );
                    if (resp.code !== 200) {
                        return this.$message.error(respeta.msg);
                    }
                    this.$refs.editFormRef.resetFields();
                    this.editDiglogVisable = false;
                    this.$message.success("");
                    this.getIpList();
                });
            },

            async deleteById(id) {
                const confirmResult = await this.$confirm(
                    ", ?",
                    "",
                    {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    }
                ).catch(err => err);
                if (confirmResult !== "confirm") {
                    return this.$message.info("");
                }
                const {data: resp} = await this.$http.delete("/delcron/" + id);
                if (resp.code !== 200) {
                    return this.$message.error(resp.msg);
                }
                this.$message.success("");
                this.getIpList();
            },
            gototasklog(id) {
                this.$router.push({path: `/tasklog/${id}`});
            }
        }
    };
</script>

