<script>
import qs from "qs";
export default {
  data() {
    return {
      queryInfo: {
        ip: "",
        port: "",
        pagenum: 1,
        // 
        pagesize: 10
      },

      iplist: [],
      total: 0,
      addDiglogVisable: false,
      addForm: {
        ip: ""
      }
    };
  },
  created() {
    this.getIpList();
  },
  methods: {
    formatterStatus(val) {
      if (val == '0') {
        return ''
      } else if (val == 1) {
        return ''
      } else if (val == '3') {
        return ''
      } else if (val == '4') {
        return ''
      }
    },
    async getIpList() {
      try {
        const { data: res } = await this.$http.get("/log", {
          params: this.queryInfo
        });
        if (res.code !== 200) {
          return this.$message.error("");
        }
        this.iplist = res.data.lists;
        this.total = res.data.total;
      } catch (error) {
        return this.$message.error("cookie");
        error.message; // "Oops!"
      }
    },

    // pageSize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      console.log(newSize);
      this.getIpList();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      console.log(newPage);
      this.getIpList();
    }
  }
};
</script>

