<script>
    import qs from "qs";

    export default {
        data() {
            return {
                // 
                queryInfo: {
                    url: "",
                    poc: "",
                    snapshot: "",
                    pagenum: 1,
                    // 
                    pagesize: 10
                },
                iplist: [],
                total: 0,

            };
        },
        created() {
            this.getIpList();
        },
        methods: {
            async getIpList() {
                try {
                    const {data: res} = await this.$http.get("/gerXrayRes", {
                        params: this.queryInfo
                    });
                    if (res.code !== 200) {
                        return this.$message.error("");
                    }
                    this.iplist = res.data.lists;
                    this.total = res.data.total;
                } catch (error) {
                    return this.$message.error("cookie");
                    error.message; // "Oops!"
                }
            },
            async getIpListSingle() {
                const {data: res} = await this.$http.get("/gerXrayRes", {
                    params: this.queryInfo
                });
                if (res.code !== 200) {
                    return this.$message.error("");
                }
                this.iplist = res.data.lists;
                this.total = res.data.total;
            },
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pagesize = newSize;
                console.log(newSize);
                this.getIpList();
            },
            // 
            handleCurrentChange(newPage) {
                this.queryInfo.pagenum = newPage;
                console.log(newPage);
                this.getIpList();
            },

            //
            diglogClose(operation) {
                if (operation === "add") {
                    this.$refs.addFormRef.resetFields();
                } else if (operation === "edit") {
                    this.$refs.editFormRef.resetFields();
                }
            },

            async deleteById(id) {
                const confirmResult = await this.$confirm(
                    ", ?",
                    "",
                    {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    }
                ).catch(err => err);
                if (confirmResult !== "confirm") {
                    return this.$message.info("");
                }
                const { data: resp } = await this.$http.delete("/delxrayres/" + id);
                if (resp.code !== 200) {
                    return this.$message.error(resp.msg);
                }
                this.$message.success("");
                this.getIpList();
            },

            editListInfo() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    const {
                        data: resp
                    } = await this.$http.put(
                        `/jobiplist/${this.editForm.id}`,
                        qs.stringify(this.editForm),
                        {headers: {"Content-Type": "application/x-www-form-urlencoded"}}
                    );
                    if (resp.code !== 200) {
                        return this.$message.error(respeta.msg);
                    }
                    this.$refs.editFormRef.resetFields();
                    this.editDiglogVisable = false;
                    this.$message.success("");
                    this.getIpList();
                });
            }

        }
    };
</script>

