<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <el-form :model="loginForm" :rules="loginRules" ref="loginFormRef" label-width="0px" class="login_form">
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="password">
          <!-- <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" :type="pwdType"> -->
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password">
            <i slot="suffix" class="iconfont icon-showpassword" @click="showPwd"></i>
          </el-input>
        </el-form-item>

        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: '',
        password: ''
      },
      loginFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 16, message: ' 3  16 ', trigger: 'blur' }
        ],
        pwdType: 'password'
      }
    }
  },
  methods: {
    showPwd() {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },

    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: resp } = await this.$http.get('/auth?username=' + this.loginForm.username + '&password=' + this.loginForm.password)
        // console.log(resp);
        if (resp.code !== 200) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          window.sessionStorage.setItem('token', resp.data.token)
          this.$router.push('/home')
        }        
        // http://127.0.0.1:8000/auth?username=drunk&password=@plin1998.
        // if (resp.meta.status !== 200) {
        //   return this.$message.error('')
        // } else {
        //   // 1.tokensessionStorage
        //   //  1.1 API
        //   //  1.2 token tokensessionStorage
        //   this.$message.success('')
        // }
      })
    }
  }
}
</script>

